const FilterSelect = ({ items, ...restProps }: any) => {
  return (
    <Select
      variant="outlined"
      style={{ width: '100%' }}
      renderValue={(selected: any) => {
        if (selected != null) {
          const value: any = items.find((item: any) => {
            return item.value === selected;
          });
          if (value) {
            return value.label;
          } else {
            return '——';
          }
        }
        return '——';
      }}
      {...restProps}
    >
      {items.map((item: any, i: number) => (
        <MenuItem key={i} value={item.value}>
          {item.label}
        </MenuItem>
      ))}
    </Select>
  );
};

const popoverStyles = makeStyles((theme: Theme) => ({
  popoverCheckbox: {
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
  },
}));

interface SelectColumnsPopoverProps {
  columns: any[];
  selectedColumns: any[];
  setSelectedColumns: any;
}

const SelectColumnsPopover: React.FC<SelectColumnsPopoverProps> = (
  props: SelectColumnsPopoverProps,
): JSX.Element => {
  const { columns, selectedColumns, setSelectedColumns } = props;
  const classes = popoverStyles(props);

  const onCheckChange = (columnObj: any) => (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    if (event.target.checked) {
      const tmpSelectedColumns = [...selectedColumns];
      tmpSelectedColumns.push(columnObj);

      setSelectedColumns(tmpSelectedColumns);
    } else {
      const tmpSelectedColumns = [...selectedColumns];
      remove(tmpSelectedColumns, columnName => {
        return columnName.name === columnObj.name;
      });

      setSelectedColumns(tmpSelectedColumns);
    }
  };

  const popupState = usePopupState({
    variant: 'popover',
    popupId: 'columnListPopover',
  });

  return (
    <div>
      <Button fullWidth {...bindTrigger(popupState)}>
        <Typography className={clsx('link-button')} align="right">
          <FontAwesomeIcon
            icon={faThList}
            className={clsx('link-button-icon')}
          />
          <span className={clsx('link-button-text')}>Display Columns</span>
        </Typography>
      </Button>
      <Popover
        {...bindPopover(popupState)}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
      >
        <FormGroup>
          {map(columns, (column, idx) => {
            const checked =
              findIndex(selectedColumns, { name: column.name }) >= 0;

            return (
              <FormControlLabel
                key={idx}
                control={
                  <Checkbox
                    checked={checked}
                    onChange={onCheckChange(column)}
                    value={column.name}
                    color="primary"
                  />
                }
                className={classes.popoverCheckbox}
                label={column.title}
              />
            );
          })}
        </FormGroup>
      </Popover>
    </div>
  );
};

