  getTicketStats = () => {
    const tickets = this.state.rows;
    this.setState({
      ticketStats: {
        all: tickets.length,
        open: tickets.filter(ticket => ticket.status.toLowerCase() === 'open')
          .length,
        closed: tickets.filter(
          ticket => ticket.status.toLowerCase() === 'closed',
        ).length,
        resolved: tickets.filter(
          ticket => ticket.status.toLowerCase() === 'resolved',
        ).length,
        pending: tickets.filter(
          ticket => ticket.status.toLowerCase() === 'pending',
        ).length,
      },
    });
  };

